import Vue from 'vue'
import Router from 'vue-router'
import Start from '@/components/pages/StartPage'
import Companies from '@/components/pages/CompaniesListPage'
import NewCompany from '@/components/pages/NewCompaniesPage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Start',
      redirect: '/sign_in'
    },
    {
      path: '/sign_in',
      name: 'Sign in',
      component: Start
    },
    {
      path: '/companies',
      name: 'Companies',
      component: Companies
    },
    {
      path: '/companies/new',
      name: 'NewCompany',
      component: NewCompany
    }
  ]
})



