<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-md-4">
        <ul class="nav nav-tabs justify-content-around">
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{'active': mode==='login'}"
              data-toggle="tab"
              @click="mode='login'"
            >
              Login
            </a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{'active': mode==='register'}"
              data-toggle="tab"
              @click="mode='register'"
            >
              Register
            </a>
          </li>
        </ul>
        <div class="tab-content">
          <div v-if="mode==='login'" class="active">
            <form>
              <div class="form-group">
                <div class="form-group">
                  <label for="login">Email</label>
                  <input class="form-control" id="login" aria-describedby="emailHelp" v-model="credentials.email">
                </div>
                <div class="form-group">
                  <label for="password">Password</label>
                  <div class="input-group">
                    <input type="password" class="form-control" id="password" v-model="credentials.password">
                  </div>
                </div>
                <div class="form-group d-flex">
                  <button type="button" class="btn btn-primary" @click="signIn()">Sign in</button>
                </div>
              </div>
            </form>
          </div>
          <div v-if="mode==='register'">
            <form>
              <div class="form-group">
                <div class="form-group">
                  <label for="register-email">Email</label>
                  <input type="email" class="form-control" id="register-email" v-model="credentials.email"/>
                </div>
                <label>Select your role</label>
                <select class="browser-default custom-select form-group" v-model="credentials.role">
                  <option value="client">Client</option>
                  <option value="company">Company</option>
                </select>
                <div class="form-group">
                  <label for="register-password">Password</label>
                  <input type="password" class="form-control" id="register-password" v-model="credentials.password"/>
                </div>
                <div class="form-group">
                  <label for="register-password-confirmation">Password confirmation</label>
                  <input type="password" class="form-control" id="register-password-confirmation" v-model="passwordConfirm"/>
                </div>
                <div class="form-group d-flex">
                  <button type="button" class="btn btn-primary" @click="register()">Register</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import UserService from '@/services/UserService'

  export default {
    name: 'StartPage',
    data() {
      return {
        credentials: {
          email: '',
          password: '',
          role: 'client'
        },
        mode: 'login',
        passwordConfirm: ''
      }
    },
    methods: {
      signIn() {

      },
      async register() {
        if (this.credentials.email !== '' && this.credentials.role !== '' && this.credentials.password !== '') {
          if (this.credentials.password === this.passwordConfirm) {
            await UserService.addNewUser({
              email: this.credentials.email,
              role: this.credentials.role,
              password: this.credentials.password
            })
            if (this.credentials.role === 'client') {
              this.$router.push({name:'Companies'})
            } else {
              this.$router.push({name: 'NewCompany'})
            }
          } else {
            alert('Wrong password confirmation')
          }
        } else {
          alert('Empty fields')
        }
      }
    }
  }
</script>

<style scoped>

</style>



